<template>
  <div>
    <v-card class="friend__find">
      <v-card-title class="friend__find__title">Поиск собеседника</v-card-title>
      <div class="friend__find__search d-flex flex-row">
        <v-text-field 
          class="friend__find__search__input" 
          color="myPurple" 
          label="Введите имя и фамилию"
          clearable
          v-model="name"
          @keydown.enter="friendSearch">
        </v-text-field>
        <v-icon class="friend__find__search__logo" @click="friendSearch" size="48">
          search
        </v-icon>
      </div>
      <div class="friend__find__list" v-for="(friend, index) of friendsList" :key="index">
        <div 
          v-if="friend.id != dataDB.id && dataDB.blackList.includes(friend.id) == false" 
          class="friend__find__list__item" 
          @mousemove="friendView(friend)"
        >
          <div class="friend__find__list__left d-flex flex-row align-center">
            <v-img 
              class="friend__find__list__image"
              v-if="friend.additionally.img" 
              :src="'/' + friend.additionally.img"
            >
            </v-img>
            <div v-else class="friend__find__list__image"></div>
            <div class="d-flex flex-column">
              <div class="friend__find__list__names d-flex flex-row align-center">
                <h2 
                  class="friend__find__list__name" 
                  @click="$router.push('/users/' + friend.id)"
                >
                  {{ friend.firstname }} {{ friend.lastname }}
                </h2>
                 <v-tooltip top>
                  <template v-slot:activator="{ on }">
                    <v-chip class="friend__find__list__level" v-on="on" color="myPurple">
                      {{ friend.level }}
                    </v-chip>
                  </template>
                  <span>{{ rank }}</span>
                </v-tooltip>
              </div>
              <p class="friend__find__list__about" v-if="friend.additionally.about">
                {{ friend.additionally.about }}
              </p>
              <v-btn 
                color="myPurple" 
                class="friend__find__list__invite"
                @click="friendDelete(friend)"
                v-if="friend.subscrise.includes(dataDB.id)"
              >
                Отменить запрос на дружбу
              </v-btn>
              <v-btn 
                color="myPurple" 
                class="friend__find__list__invite"
                @click="friendInvite(friend)"
                v-if="!friend.subscrise.includes(dataDB.id) 
                  && !friend.friends.includes(dataDB.id)"
              >
                Предложить дружбу
              </v-btn>
              <v-btn 
                color="error" 
                class="friend__find__list__invite"
                @click="friendRemove(friend)"
                v-if="friend.friends.includes(dataDB.id)"
              >
                Удалить из друзей
              </v-btn>
            </div>
          </div>
        </div>
      </div>
    </v-card>
    <v-snackbar v-model="isFriend" :timeout="timeout">
      <span>Запрос на дружбу отправлен</span>
      <v-btn color="myPurple" style="color: #fff;" @click="isFriend = false">Закрыть</v-btn>
    </v-snackbar>
    <v-snackbar v-model="isFriendDelete" :timeout="timeout">
      <span>Вы отменили запрос на добавление в друзья</span>
      <v-btn color="myPurple" style="color: #fff;" @click="isFriendDelete = false">
        Закрыть
      </v-btn>
    </v-snackbar>
    <v-snackbar v-model="isFriendRemove" :timeout="timeout">
      <span>Данный пользователь удален из друзей</span>
      <v-btn color="myPurple" style="color: #fff;" @click="isFriendRemove = false">
        Закрыть
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import { RankList } from "../../models/models.js";
import axios from "axios";
import "../../scss/friendFind.scss";
export default {
  name: "FriendFind",
  data() {
    return {
      friendsList: "",
      name: "",
      firstname: "",
      lastname: "",
      rank: "",
      dataDB: "",
      isFriend: false,
      isFriendDelete: false,
      isFriendRemove: false,
      timeout: 2000
    }
  },
  created() {
    this.dataDB = JSON.parse(localStorage.getItem("user"))
  },
  methods: {
    async friendSearch() {
      let isTrue = false
      this.name = this.name.trimStart().trimEnd()
      this.name.split('')
      for (let i = 0; i < this.name.length; i++) {
        if (this.name[i] == " ") break
        else this.firstname += this.name[i]
      }
      for (let i = 0; i < this.name.length; i++) {
        if (this.name[i] == " ") {
          isTrue = true
          continue
        }
        if (isTrue) this.lastname += this.name[i]
      }
      try {
        let response = await axios.post("/api/users/get_friends",
          { firstname: this.firstname, lastname: this.lastname })
        this.firstname = ""
        this.lastname = ""
        this.friendsList = response.data
      } catch (error) {
        console.log(error.message)
      }
    },
    friendView(friend) {
      let ranks = new RankList()
      ranks.rankLevel = friend.level 
      this.rank = ranks.rankLevel
    },
    async friendInvite(friend) {
      await axios.put("/api/users/friend_add/" + friend.id + '/' + this.dataDB.id)
      this.isFriend = true 
    },
    async friendDelete(friend) {
      await axios.delete("/api/users/friend_cancel/" + friend.id + '/' + this.dataDB.id)
      this.isFriendDelete = true
    },
    async friendRemove(friend) {
      let res = await axios.delete("/api/users/friend_remove/" + 
        friend.id + '/' + this.dataDB.id) 
      localStorage.setItem("user", JSON.stringify(res.data))
      this.isFriendRemove = true
    }
  }
}
</script>