<template>
  <div>
    <div class="subscrise">
      <div v-if="subsList.length > 0">
        <div class="subscrise__list" v-for="(sub, index) of subsList" :key="index">
          <div class="subscrise__item">
            <Loader v-if="isLoading" />
            <div class="subscrise__item__left d-flex flex-row">
              <v-img 
                v-if="sub.additionally.img" 
                class="subscrise__item__logo"
                :src="'/' + sub.additionally.img"
              ></v-img>
              <div v-else class="subscrise__item__logo"></div>
              <div class="subscrise__item__title d-flex flex-column">
                <router-link 
                  class="subscrise__item__title__name" 
                  :to="{name: 'Account', params: {id: sub.id}}"
                >
                  {{ sub.firstname }} {{ sub.lastname }}
                </router-link>
                <div class="d-flex flex-row">
                  <v-btn 
                    @click="friendAccept(sub)" 
                    class="subscrise__item__accept" 
                    color="myPurple"
                  >
                    Принять в друзья
                  </v-btn>
                  <v-btn 
                    class="subscrise__item__accept" 
                    color="error" 
                    @click="isBanned = true; isSubBan = sub"
                  >
                    Заблокировать
                  </v-btn>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="subscrise__nosubs d-flex flex-row justify-center align-center" 
      v-else-if="subsList.length == 0 && !isLoading">
        <h2>У вас отсутствуют подписчики.</h2>
        <v-img src="../assets/nopost.png" max-width="300" height="400"></v-img>
      </div>
    </div>
    <v-dialog v-model="isBanned" width="700px">
      <v-card>
        <v-card-title>
          Вы уверены, что хотите заблокировать данного пользователя?
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error" outlined @click="isBanned = false; isSubBan = ''">
            Отмена
          </v-btn>
          <v-btn color="myPurple" outlined @click="isBanned = false; userBlocking()">
            Заблокировать
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="isBanUser" :timeout="timer">
      Данный пользователь был заблокирован
      <v-btn color="myPurple" @click="isBanUser = false" style="color: #fff !important;">
        Закрыть
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import Loader from "../components/Loader.vue";
import axios from "axios";
import "../scss/subscriseList.scss";

export default {
  name: "SubscrisesList",
  components: { Loader },
  data() {
    return {
      user: "",
      subs: [],
      subsList: [],
      isBanned: false,
      isSubBan: "",
      isBanUser: false,
      isLoading: true,
      timer: 2000
    }
  },
  props: {
    dataDB: Object
  },
  async created() {
    let response = await axios.get("/api/users/get_subscrises/" + this.dataDB.id)
    this.subs = response.data
    for (let sub of this.subs) {
      let res = await axios.get('/api/users/find_user/' + sub)
      this.subsList.push(res.data)
    }
    this.isLoading = false
  },
  methods: {
    async userBlocking() {
      let res = await axios.put("/api/users/blacklist_add/" + 
        this.isSubBan.id + '/' + this.dataDB.id)
      this.isBanUser = true
      localStorage.setItem("user", JSON.stringify(res.data))
    },
    async friendAccept(sub) {
      let res = await axios.put("/api/users/friend_accept/" + sub.id + '/' + this.dataDB.id)
      localStorage.setItem("user", JSON.stringify(res.data))
    }
  }
};
</script>
