<template>
  <div>
    <Loader v-if="isLoading" />
    <v-text-field 
      color="myPurple" 
      label="Поиск ваших друзей" 
      clearable 
      v-model="name"
      @keydown.enter="friendFind"
      solo></v-text-field>
    <div v-if="friendsList.length > 0">
    <div
      class="friend__left"
      v-for="(friend, index) of 
        (name.length == 0 || this.search.length == 0) ? this.friendsList : this.search"
      :key="index"
    >
      <div class="friend__left__inner d-flex align-center">
        <v-img 
          v-if="friend.additionally.img" 
          class="friend__left__img"
          :src="'/' + friend.additionally.img"
        ></v-img>
        <div v-else class="friend__left__img"></div>
        <div class="friend__left__text d-flex flex-column">
          <router-link 
            class="friend__left__text__title" 
            :to="{name: 'Account', params: {id: friend.id}}"
          >
            {{ friend.firstname }} {{ friend.lastname }}
          </router-link>
          <v-btn @click="chatCreate(friend)" color="myPurple" class="friend__left__text__btn">
            Написать сообщение
          </v-btn>
        </div>
        <div class="friend__left__menu__wrapper">
          <v-menu>
            <template v-slot:activator="{ on }">
              <v-btn dark icon v-on="on">
                <v-icon size="40" class="friend__left__menu"
                  >mdi-dots-vertical</v-icon
                >
              </v-btn>
            </template>
            <v-list>
              <v-list-item @click="friendDelete(friend)" style="cursor: pointer;">
                <v-list-item-title>Удалить из друзей</v-list-item-title>
              </v-list-item>
              <v-list-item @click="friendBan(friend)" style="cursor: pointer;">
                <v-list-item-title>Заблокировать</v-list-item-title>
              </v-list-item>
              <v-list-item style="cursor: pointer;">
                <v-list-item-title>Общие друзья</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </div>
      </div>
    </div>
    </div>
    <div v-else class="d-flex flex-row align-center" style="margin-top: 30px;">
      <div>
        <h2 style="margin-right: 30px;">Ваши друзья отсутствуют</h2>
        <p style="margin-left: 35px;">Вы еще не добавили ни одного друга</p>
      </div>
      <v-img src="../../assets/artgoat.svg" width="200" height="200"></v-img>
    </div>
  </div>
</template>

<script>
import Loader from "../Loader.vue";
import axios from "axios";
import "../../scss/friendList.scss";

export default {
  name: "Friends",
  components: { Loader },
  data() {
    return {
      dataDB: "",
      isLoading: true,
      friendsList: [],
      name: "",
      firstname: "",
      lastname: "",
      search: ""
    }
  },
  async created() {
    this.dataDB = JSON.parse(localStorage.getItem("user"))
    try {
      let res = await axios.get("/api/users/find_friends/" + this.dataDB.friends)
      if (res.data.length > 0) this.friendsList = res.data
      this.isLoading = false
    } catch (error) {
      this.isLoading = false
    }
  },
  methods: {
    async friendDelete(friend) {
      let res = await axios.delete("/api/users/friend_remove/" + 
        friend.id + '/' + this.dataDB.id)
      localStorage.setItem("user", JSON.stringify(res.data))
    },
    async friendFind() {
      this.search = ""
      this.name = this.name.trimLeft().trimRight().split(' ')
      this.firstname = this.name[0]
      this.lastname = this.name[1]
      this.name = this.name.join(' ')
      try {
        let res = await axios.get("/api/users/find_friend/" + 
          this.dataDB.id + '/' + this.firstname + '/' + this.lastname)
        this.search = res.data
      } catch(err) {

        console.log(err.message)
      }
    },
    async friendBan(friend) {
      let res = await axios.put("/api/users/blacklist_add/" + 
        friend.id + '/' + this.dataDB.id)
      localStorage.setItem("user", JSON.stringify(res.data))
    },
    async chatCreate(friend) {
      let res = await axios.post("/api/chat/chat-create", 
        { userId: friend.id, myId: this.dataDB.id })
      this.$router.push({ name: 'Chat', params: { id: res.data.chatId }})
    }
  }
};
</script>