<template>
  <div>
    <NotAuth>
      <template v-slot:error></template>
      <template v-slot:loading></template>
      <template v-slot:default>
        <div class="friend__wrapper d-flex flex-row">
          <div style="margin-left: 20px; margin-top: 100px;">
            <Panel />
          </div>
          <div class="friend">
            <v-card class="friend__list" color="#fff">
              <v-tabs color="myPurple" class="d-flex justify-center friend__tabs">
                <v-tab @click="isFriends = true" class="friend__title">Друзья</v-tab>
                <v-tab @click="isFriends = false" class="friend__title">Подписчики</v-tab>
              </v-tabs>
              <FriendList v-if="isFriends" :dataDB="dataDB" />
              <SubscrisesList v-else :dataDB="dataDB" />
            </v-card>
          </div>
        </div>
      </template>
    </NotAuth>
  </div>
</template>

<script>
import Panel from "../components/Panel.vue";
import FriendList from "../components/FriendList.vue";
import SubscrisesList from "../components/SubscrisesList.vue";
import NotAuth from "../components/NotAuth.vue";

export default {
  name: "FriendsList",
  metaInfo: {
    title: "MyOcean - Список друзей"
  },
  components: {
    Panel,
    FriendList,
    SubscrisesList,
    NotAuth
  },
  data() {
    return {
      isFriends: true,
      dataDB: ""
    }
  },
  created() {
    this.dataDB = JSON.parse(localStorage.getItem("user"))
  }
};
</script>

<style lang="scss" scoped>
$white: #fff;
.friend {
  margin-top: 100px;
  min-height: 100vh;
  &__tabs {
    background-color: #fff;
  }
  &__list {
    width: 1170px;
    min-height: 640px;
    
  }
  &__title {
    font-size: 24px;
    background-color: $white;
    font-weight: normal;
    text-transform: capitalize;
  }
}
</style>
